.batman-store {
  &__header-nonprofile {
    margin: 40px;
    padding: 10px;
    box-sizing: border-box;
    border: 1px solid transparent;
    cursor: pointer;
    transition: 0.5s;
    & > a {
      display: flex;
      text-align: center;
      cursor: pointer;
      transition: 0.3s;
      border-right: 1px solid transparent;
      color: #11142d;
      text-decoration: none;
      align-items: center;
      img {
        margin-left: 5px;
        width: 20px;
        height: 20px;
      }
    }
  }
  &__header-container_logo {
    & > a {
      & > img {
        margin: 0 auto;
        width: 150px;
      }
    }
  }
  &__header-nonprofile:hover {
    border-bottom: 1px solid #11142d;
    box-sizing: border-box;
  }
}
